<template>
  <div class="summary-bar-wrapper">
    <div v-if="route == 'Home'" class="summation-label">
    {{$t('Added')}} <span class="counter">{{ currentlySelectedLength }}</span>  {{$t('Products')}}
    </div>
    <div @click="handleBack" v-else class="summation-label"> {{$t('Back')}}</div>
    <div v-if="route == 'Summary'" @click="generatePDF" class="next">
       {{$t('Generate')}}
      <img
        class="arrow-icon"
        alt="arrow icon"
        src="../../assets/arrow-left.svg"
      />
    </div>
    <div v-else @click="handleProductSummary" class="next">
       {{$t('Next')}}
      <img
        class="arrow-icon"
        alt="arrow icon"
        src="../../assets/arrow-left.svg"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "SummaryBar",
  props: {
    currentlySelectedLength: {
      type: Number,
      default: null,
    },
    route: {
      type: String,
      default: "",
    },
  },
  methods: {
    handleBack() {
      this.$router.push({ name: this.route == "summary" ? "Products" : "Home"});
    },
    handleProductSummary() {
      const payload = {
        type: this.route,
      };
      this.$emit("summary", payload);
    },
    generatePDF() {
      console.log("GENERUJE PDF...");
    },
  },
};
</script>

